<template>
  <transition name="slide">
    <div class="wrapper">
      <div class="header">
        <div class="icon-wrapper">
          <i class="back" @click="back"></i>
        </div>
        <div class="search-box">
          <search-box @query="onQueryChange" placeholder="请输入日期/关键词"></search-box>
        </div>
      </div>
      <div class="content-wrapper">
        <scroll class="content">
          <ul class="list">
            <li class="item" v-for="(item,index) in list" @click="selectItem(item)">
              <div class="top">
                <span>{{item.time}}</span>
              </div>
              <div class="middle">
                <p class="title">账单总额</p>
                <p class="price">￥<span>{{item.total}}</span></p>
                <div class="money-detail">
                  <div class="left">
                    <label>应还款金额</label>
                    <p>￥<span>3200</span></p>
                  </div>
                  <div class="right">
                    <label>已还款金额</label>
                    <p>￥<span>300</span></p>
                  </div>
                </div>
              </div>
              <div class="bottom">
                <ul class="detail">
                  <li class="detail-item">
                    <label>应还款ID</label>
                    <span>{{item.shouldId}}</span>
                  </li>
                  <li class="detail-item">
                    <label>订单ID</label>
                    <span>{{item.orderId}}</span>
                  </li>
                  <li class="detail-item">
                    <label>应还款金额</label>
                    <span>￥{{item.shouldPay}}</span>
                  </li>
                </ul>
                <i class="forward" @click="goToPayAccount"></i>
              </div>
            </li>
          </ul>
        </scroll>
      </div>
      <router-view></router-view>
    </div>
  </transition>
</template>

<script type="text/ecmascript-6">
  import SearchBox from 'base/search-box/search-box'
  import Scroll from 'base/scroll/scroll'
  import {ERR_CODE} from 'api/config'
  import {getMerchantSettlementOrder} from 'api/features'

  export default {
    data () {
      return {
        query: '',
        list: []
      }
    },
    created () {
      this._getMerchantSettlementOrder()
    },
    computed: {
      currentRoute(){
        return this.$router.currentRoute.path
      }
    },
    methods: {
      back() {
        this.$router.back()
      },
      goToPayAccount() {
        this.$router.push({
          path: `${this.currentRoute}/detail`
        })
      },
      showCategory() {

      },
      selectItem(item) {
        console.log(item)
      },
      _getMerchantSettlementOrder() {
        getMerchantSettlementOrder().then((res) => {
          if(res.errCode === ERR_CODE){
            this.list = res.data
          }
        })
      },
      onQueryChange(newQuery) {
        this.query = newQuery
      }
    },
    watch: {
      query(){
        this._getMerchantSettlementOrder()
      }
    },
    components: {
      SearchBox,
      Scroll
    }
  }
</script>

<style scoped lang="stylus" rel="stylesheet/stylus">
  @import "~common/stylus/variable"

  .slide-enter-active,.slide-leave-active
    transition all 0.3s
  .slide-enter,.slide-leave-to
    transform translate3d(100%,0,0)

  .wrapper
    background $color-background
    position absolute
    left 0
    right 0
    top 0
    bottom 0
    z-index 1
    .header
      position fixed
      height 44px
      top 0
      left 0
      right 0
      background $color-header-background
      display flex
      justify-content center
      align-items center
      .icon-wrapper
        width 50px
        height 100%
        .back
          position absolute
          background url("~common/image/back.png") no-repeat
          background-size contain
          width 8px
          height 15px
          left 15px
          top 15px
          &:before
            position absolute
            left -10px
            right -10px
            top -10px
            bottom -10px
            content ''
      .search-box
        display flex
        flex 1
        margin-right 33px
    .content-wrapper
      position fixed
      left 0
      right 0
      top 44px
      bottom 0
      background $color-background
      .content
        height 100%
        overflow hidden
        margin-left 15px
        margin-right 15px
        .list
          width 100%
          .item
            display flex
            flex-direction column
            align-items center
            height 308px
            box-sizing border-box
            padding-left 15px
            padding-right 15px
            margin-top 10px
            background #fff
            border-radius 3px
            border 1px solid #e6e6e6
            &:last-child
              margin-bottom 10px
            .top
              width 100%
              display flex
              justify-content space-between
              margin-top 18px
              color #666
              font-size $font-size-small-x
            .middle
              margin-top 30px
              width 100%
              display flex
              flex 1
              flex-direction column
              .title
                text-align center
                font-size $font-size-small-x
                color #999
              .price
                text-align center
                color #333
                font-weight bold
                font-size $font-size-small-x
                margin-top 16px
                span
                  font-size $font-size-large-m
                  font-weight bold
              .money-detail
                display flex
                justify-content space-between
                margin 32px 30px 0 30px
                .left,.right
                  display flex
                  flex-direction column
                  label
                    font-size $font-size-small-x
                    color #999
                  p
                    margin-top 20px
                    font-size $font-size-small-x
                    span
                      font-weight bold
                      font-size $font-size-large
                .left p
                  color #eb4747
            .bottom
              display flex
              width 100%
              align-items center
              border-top 1px solid #f2f2f2
              position relative
              .detail
                display flex
                flex-direction column
                width 100%
                .detail-item
                  height 32px
                  display flex
                  align-items center
                  label
                    font-size $font-size-small-x
                    color #999
                    width 120px
                  span
                    font-size $font-size-medium
                    color #333
              .forward
                background url("~common/image/forward.png") no-repeat
                background-size contain
                margin-left 18px
                width 6px
                height 10px
                position absolute
                right 0
                &:before
                  position absolute
                  left -10px
                  top -10px
                  right -10px
                  bottom -10px
                  content ''

</style>



